const audios = {
  'item': new Audio('/assets/item.mp3'),
  'select': new Audio('/assets/select.mp3'),
};


export const playAudio = (name, ext = 'mp3') => {
  // let audio = new Audio(`/wedding-svelte/assets/${name}.${ext}`);
  console.log(name);
  audios[name].currentTime = 0;
  audios[name].play();
}